import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { JobService } from 'src/app/service/job.service';

@Component({
  selector: 'app-jobleve',
  templateUrl: './jobleve.component.html',
  styleUrls: ['./jobleve.component.scss']
})
export class JobleveComponent implements OnInit {
  idLevel:any;
  constructor(private jobService: JobService,private _Activatedroute:ActivatedRoute) { }

  ngOnInit() {
    this.idLevel=this._Activatedroute.snapshot.params['id'];
    this.jobService.GetJobLevel(this.idLevel)
    .subscribe(res =>{
        console.log(res);
    });
  }

}
